import React from "react";
import thirtythree from "./33.png";
import {
  Breadcrumb,
  BreadcrumbItem,
  Card,
  CardImg,
  CardBody,
  CardTitle,
} from "reactstrap";
import { Link } from "react-router-dom";

const TechDetail3 = () => {
  return (
    <div className="container">
      <div className="row p-2">
        <Breadcrumb>
          <BreadcrumbItem>
            <Link to="/home">Home</Link>
          </BreadcrumbItem>
          <BreadcrumbItem>
            <Link to="/tech">Tech</Link>
          </BreadcrumbItem>
          <BreadcrumbItem active>Read</BreadcrumbItem>
        </Breadcrumb>
      </div>
      <div className="row p-3">
        <div className="col-12">
          <Card>
            <CardImg src={thirtythree} />
            <CardBody>
              <CardTitle className="paragraph">
                China is all set to build the First Waterless Nuclear Reactor of
                the World
              </CardTitle>
            </CardBody>
          </Card>
        </div>
      </div>
      <div className="row justify-content-center">
        <div className="col-9">
          <p>
            Great To Love YouChinese government experts, according to sources,
            have revealed a novel design for a commercial molten salt nuclear
            reactor. It would not require any water for cooling, yet it is
            projected to be the first waterless nuclear reactor in the world.
          </p>
          <p>
            Officials claim that the reactor does not require water and that it
            may be deployed in abandoned places or countries where water is
            scarce. It also enables operators to utilise it in sparsely
            populated areas to provide power to a large population.
          </p>
        </div>
      </div>
    </div>
  );
};

export default TechDetail3;
