import React from "react";
import thirtyfour from "./34.png";
import {
  Breadcrumb,
  BreadcrumbItem,
  Card,
  CardImg,
  CardBody,
  CardTitle,
} from "reactstrap";
import { Link } from "react-router-dom";

const TechDetail4 = () => {
  return (
    <div className="container">
      <div className="row p-2">
        <Breadcrumb>
          <BreadcrumbItem>
            <Link to="/home">Home</Link>
          </BreadcrumbItem>
          <BreadcrumbItem>
            <Link to="/tech">Tech</Link>
          </BreadcrumbItem>
          <BreadcrumbItem active>Read</BreadcrumbItem>
        </Breadcrumb>
      </div>
      <div className="row p-3">
        <div className="col-12">
          <Card>
            <CardImg src={thirtyfour} />
            <CardBody>
              <CardTitle className="paragraph">
                The private data of human rights activists, journalists, and
                lawyers is reportedly under threat globally.
              </CardTitle>
            </CardBody>
          </Card>
        </div>
      </div>
      <div className="row justify-content-center">
        <div className="col-9">
          <p>
            The private data of human rights activists, journalists, and lawyers
            is reportedly under threat globally. Leaked records suggest as many
            as 1,000 have been targeted by phone malware sold to governments by
            an Israeli surveillance firm. Stories of big tech companies and
            anti-democratic governments butting heads have become common over
            the last few years. However, if new reports are to be believed, the
            whole data war may have taken its most sinister turn yet.
          </p>
          <p>
            According to Paris-based anti-censorship outfit Forbidden Stories,
            mobile spyware is being discreetly sold to authoritarian government
            leaders to hijack the private data of activists, journalists,
            lawyers, and even heads of state. Backed by the technical nous of
            Amnesty International, Forbidden Stories coordinated forensic tests
            on the mobile phones of reported targets to identify traces of any
            potential spyware. Expecting to find the odd data violation, the
            mission instead unearthed a full-scale assault on democracy.
          </p>
        </div>
      </div>
    </div>
  );
};

export default TechDetail4;
