import React from "react";
import twentyfour from "./24.png";
import {
  Breadcrumb,
  BreadcrumbItem,
  Card,
  CardImg,
  CardBody,
  CardTitle,
} from "reactstrap";
import { Link } from "react-router-dom";

const CultureDetail4 = () => {
  return (
    <div className="container">
      <div className="row p-2">
        <Breadcrumb>
          <BreadcrumbItem>
            <Link to="/home">Home</Link>
          </BreadcrumbItem>
          <BreadcrumbItem>
            <Link to="/culture">Culture</Link>
          </BreadcrumbItem>
          <BreadcrumbItem active>Read</BreadcrumbItem>
        </Breadcrumb>
      </div>
      <div className="row p-3">
        <div className="col-12">
          <Card>
            <CardImg className="detail-img" src={twentyfour} />
            <CardBody>
              <CardTitle className="titling">
                It’s no-plastic July! Here, we’ve been making a conscious effort
                to cut down on our plastic consumption and recycle responsibly
                when it is unavoidable
              </CardTitle>
            </CardBody>
          </Card>
        </div>
      </div>
      <div className="row  justify-content-center">
        <div className="col-9 ">
          <p>
            As you might already know, it’s our mission to spread the word about
            positive social change, so this month we want to share 21 of the
            easiest ways to get closer to a plastic-free lifestyle. Alongside
            urging big companies to change their current methods of plastic
            production, we can all make subtle but impactful changes that reduce
            plastic in our lives. Let’s get started!
          </p>
          <p>
            1. Switch out plastic bottles for a reusable bottle: A major
            contribution to plastic pollution is single-use water bottles. Metal
            bottles like these will keep your drinks ice cold for the entire
            day. With so many options, colours, and designs to choose from,
            there’s one for everyone – continuing to buy plastic bottles is just
            silly! 2. Buy a canvas tote bag to use when shopping: A reusable
            tote bag is one of the easiest ways to cut down on your single use
            plastic habit. They look good, they’re better for the environment,
            and you can wash them as needed, too. If you’re only popping to the
            shop for a few things, carry your items home if you can. 3. Bring
            your own travel mug: Reusable coffee cups are not new, but many of
            us still opt for a single-use cup when grabbing our morning tea and
            coffee. A recent report says 7 million disposable coffee cups are
            thrown out every day in the UK alone – that’s 2.5 billion each year.
            We can do better! 4. Dodge unnecessary plastic cutlery: Many
            eateries offer wooden utensils nowadays, but if you find your local
            lunch spot still won’t ditch the plastic version, pop a fork and
            knife from home in your bag. When ordering a takeaway, be sure to
            select ‘no cutlery’ if you don’t need it.
          </p>
        </div>
      </div>
    </div>
  );
};

export default CultureDetail4;
