import React from "react";
import twentyone from "./21.png";
import {
  Breadcrumb,
  BreadcrumbItem,
  Card,
  CardImg,
  CardBody,
  CardTitle,
} from "reactstrap";
import { Link } from "react-router-dom";

const CultureDetail = () => {
  return (
    <div className="container">
      <div className="row p-2">
        <Breadcrumb>
          <BreadcrumbItem>
            <Link to="/home">Home</Link>
          </BreadcrumbItem>
          <BreadcrumbItem>
            <Link to="/culture">Culture</Link>
          </BreadcrumbItem>
          <BreadcrumbItem active>Read</BreadcrumbItem>
        </Breadcrumb>
      </div>
      <div className="row p-3">
        <div className="col-12">
          <Card>
            <CardImg className="detail-img" src={twentyone} />
            <CardBody>
              <CardTitle className="titling">
                The United Arab Emirates has donated $23 million to Pakistan’s
                polio eradication campaign
              </CardTitle>
            </CardBody>
          </Card>
        </div>
      </div>
      <div className="row  justify-content-center">
        <div className="col-9 ">
          <p className="paragraph">
            The United Arab Emirates (UAE) has donated $23 million to help
            anti-polio programmes in Pakistan through December of this year, as
            the South Asian country continues its struggle against the deadly
            illness.
          </p>
          <p>
            The World Health Organization (WHO) stated in a statement on
            Saturday on behalf of the Polio Eradication Initiative, It will
            allow the polio programme to sustain momentum in conducting
            successful monthly polio campaigns targeting 16 million children in
            Pakistan's highest-risk areas. The UAE’s latest donation comes on
            top of the $200 million it has given to Pakistan since 2014, with
            WHO Representative in Pakistan Dr. Palitha Mahipala thanking the
            UAE’s leadership for their “generous contribution.” Pakistan resumed
            anti-polio vaccines in June, months after the programme was halted
            because to the COVID-19 outbreak, which had overwhelmed the
            country's health system and risks to the campaign.
          </p>
        </div>
      </div>
    </div>
  );
};

export default CultureDetail;
