import React from "react";
import twentythree from "./23.png";
import {
  Breadcrumb,
  BreadcrumbItem,
  Card,
  CardImg,
  CardBody,
  CardTitle,
} from "reactstrap";
import { Link } from "react-router-dom";

const CultureDetail3 = () => {
  return (
    <div className="container">
      <div className="row p-2">
        <Breadcrumb>
          <BreadcrumbItem>
            <Link to="/home">Home</Link>
          </BreadcrumbItem>
          <BreadcrumbItem>
            <Link to="/culture">Culture</Link>
          </BreadcrumbItem>
          <BreadcrumbItem active>Read</BreadcrumbItem>
        </Breadcrumb>
      </div>
      <div className="row p-3">
        <div className="col-12">
          <Card>
            <CardImg className="detail-img" src={twentythree} />
            <CardBody>
              <CardTitle className="titling">
                Lets Talk About Sindh Culture
              </CardTitle>
            </CardBody>
          </Card>
        </div>
      </div>
      <div className="row  justify-content-center">
        <div className="col-9 ">
          <p>
            Sindh is the province of Pakistan, located at the southern border
            and named after one of the major rivers Indus. This province’s name
            is derived from Sindhu which means “an ocean” in the Sanskrit
            language. The area has deep roots in Indus Valley Civilization which
            was a Bronze Age civilisation and urbanized by the Dravidian
            cultures in around 3000 BC. Sindh has a larger part of the desert
            region and many natural resources. Sindhi culture is practiced by
            the Sindhi people who are divided distributed among Pakistan, India
            and diaspora. Their centuries-old history has given them strong
            traditions, customs, folklore, distinct lifestyle and their own
            language called Sindhi. But, their civilization has witnessed many
            ups and downs and it got declined because of many natural disasters
            like floods and most importantly the Indo-Aryan attack collapsed the
            whole region. According to the history, British conquered Sindh in
            1843 and till 1947, this province remained the part of British India
            and later became part of Pakistan. The Sindhi language is an ancient
            language and has solid and strong roots in Prakrit and Sanskrit.
            Sanskrit is the Indian language that has Arabic, Persian, and
            Dravidian’s vocabulary. The Sindhi language had always kept close
            contacts with Arabic-speaking Muslims and so, this language has
            adopted many words from Arabic. This language is spoken in many
            other parts of Pakistan too.
          </p>
          <p>
            There are approximately 41 million speakers in Pakistan and 12
            million speakers in India. This language is the 2nd most spoken
            language in the country and mostly spoken in Sindh. It is one of the
            recognized official languages in Pakistan as well as in India.
            Moreover, the government of Pakistan issues its citizens the
            National Card Identity in Sindhi or Urdu language. People of Sindh
            are colorful and that depicts by their wholehearted celebrations of
            Islamic and regional festivals. Folk dance of Bhagat is their
            special dance that their professionals perform to entertain the
            visitors. In simple words, this culture is a compound of music, folk
            dances, and inexpensive entertainment for local people. Sindh has
            always been considered the land of artistic values, romance, love,
            peace, and great cultural values. There lived a great mystic Sufi
            and the poet Shah Abdul Latif Bhitai. He spent his whole life
            cherishing truth, wisdom, reality of life and in the propagation of
            Islam. He also had been a great musician who invented several new
            types of musical instruments such as, Tambura.
          </p>
        </div>
      </div>
    </div>
  );
};

export default CultureDetail3;
