import React from "react";
import fourteen from "./14.png";
import {
  Breadcrumb,
  BreadcrumbItem,
  Card,
  CardImg,
  CardBody,
  CardTitle,
} from "reactstrap";
import { Link } from "react-router-dom";

const StyleDetail4 = () => {
  return (
    <div className="container">
      <div className="row p-2">
        <Breadcrumb>
          <BreadcrumbItem>
            <Link to="/home">Home</Link>
          </BreadcrumbItem>
          <BreadcrumbItem>
            <Link to="/style">Style</Link>
          </BreadcrumbItem>
          <BreadcrumbItem active>Read</BreadcrumbItem>
        </Breadcrumb>
      </div>
      <div className="row p-3">
        <div className="col-12">
          <Card>
            <CardImg className="detail-img" src={fourteen} />
            <CardBody>
              <CardTitle>
                The future looks fluid: genderless fashion explained
              </CardTitle>
            </CardBody>
          </Card>
        </div>
      </div>
      <div className="row  justify-content-center">
        <div className="col-9 ">
          <p>
            Throughout history, brands have operated in an industry dictated by
            two genders. With 27% of Gen Z consumers now identifying as
            non-conforming, is this about to change? When Harry Styles graced
            the cover of Vogue last year wearing a gown, passionate
            conversations around masculinity and gendered dressing were
            instantly sparked. Representing Gen Z’s growing exploration of
            gender-fluidity and non-binary dressing, the singer cemented his
            place as the vanguard of neutral fashion.
          </p>
          <p>
            Prominent conservatives were quick to voice their disapproval of a
            cisgender celebrity engaging in progressive developments, however.
            ‘The lines of what you should wear based on whether you’re ‘male’ or
            ‘female’ are crumbling away,’ he told the publication. ‘I’ve never
            really thought too much about what it means, it’s just this extended
            part of creating something without limitations.’ ‘Bring back manly
            men,’ tweeted political commentator Candace Owens. ‘In the west, the
            steady feminisation of our men… is an outright attack.’
          </p>
          <p>
            While this was a relatively standalone negative response (most were
            delighted to see the pop culture icon dismantle societal
            expectations for how people should present themselves) it highlights
            how contested and divisive debates around gender expression have
            become on a global scale. articularly regarding an industry which,
            throughout history, has seen brands operate strictly within the
            boundaries of standard gender categorisations. One that is only now
            beginning to adapt. This sudden shift can be attributed to the power
            of the Gen Z demographic, a consumer group that’s driving demand and
            due to account for $143 billion in spending over the next few years.
          </p>
        </div>
      </div>
    </div>
  );
};

export default StyleDetail4;
