import React from "react";
import thirtysix from "./36.png";
import {
  Breadcrumb,
  BreadcrumbItem,
  Card,
  CardImg,
  CardBody,
  CardTitle,
} from "reactstrap";
import { Link } from "react-router-dom";

const TechDetail6 = () => {
  return (
    <div className="container">
      <div className="row p-2">
        <Breadcrumb>
          <BreadcrumbItem>
            <Link to="/home">Home</Link>
          </BreadcrumbItem>
          <BreadcrumbItem>
            <Link to="/tech">Tech</Link>
          </BreadcrumbItem>
          <BreadcrumbItem active>Read</BreadcrumbItem>
        </Breadcrumb>
      </div>
      <div className="row p-3">
        <div className="col-12">
          <Card>
            <CardImg src={thirtysix} />
            <CardBody>
              <CardTitle className="paragraph">
                Celebrities, footballers, and many others have faced routine
                abuse on social media for years
              </CardTitle>
            </CardBody>
          </Card>
        </div>
      </div>
      <div className="row justify-content-center">
        <div className="col-9">
          <p>
            In September last year, an online petition was started by UK
            celebrity Katie Price to make verified ID a requirement for opening
            a social media account. This proposed legislation that would be
            called ‘Harvey’s Law’. The petition was in reaction to the frequent
            abuse and ableist attacks she receives online when posting about her
            son Harvey and his condition, Prader-Willi syndrome. Price recalls
            experiencing the ‘worst kind of abuse’ from online trolls, and
            believes that new legislation will ensure that ‘no one can hide
            behind their crime’.
          </p>
          <p>
            Recently, the petition has seen a huge soar in support following the
            defeat of England’s football team in the Euros final. The torrent of
            racist abuse faced by the three Black English players on social
            media caused understandable outrage and upset. Marcus Rashford,
            Jadon Sancho, and Bukayo Saka’s social media accounts were inundated
            with racial slurs, offensive messages, and abuse from anonymous
            trolls and spam accounts. Attackers were able to get away with their
            actions because there was no way of knowing who they were – meaning
            any consequences were limited or non-existent.
          </p>
        </div>
      </div>
    </div>
  );
};

export default TechDetail6;
