import React from "react";
import two from "./2.png";
import {
  Breadcrumb,
  BreadcrumbItem,
  Card,
  CardImg,
  CardBody,
  CardTitle,
} from "reactstrap";
import { Link } from "react-router-dom";

const HomeDetail2 = () => {
  return (
    <div className="container">
      <div className="row p-2">
        <Breadcrumb>
          <BreadcrumbItem>
            <Link to="/home">Home</Link>
          </BreadcrumbItem>
          <BreadcrumbItem active>Read</BreadcrumbItem>
        </Breadcrumb>
      </div>
      <div className="row p-3">
        <div className="col-12">
          <Card>
            <CardImg src={two} />
            <CardBody>
              <CardTitle className="paragraph">
                Iran launches ‘halal’ dating app
              </CardTitle>
            </CardBody>
          </Card>
        </div>
      </div>
      <div className="row justify-content-center">
        <div className="col-9">
          <p>
            According to a report on Iranian state media, they are planning to
            create an official halal dating app, claiming that they built the
            software with the goal of benefitting their young for "long and
            informed marriage."
          </p>
          <p>
            According to the broadcaster, they dubbed this apto benefitp
            "Hamdam," a Farsi word that means "partner," and it would let
            potential users to locate and pick their spouse. This software is
            the only state-authorized and approved app of its sort in Iran,
            according to Colonel Ali Mohammad Rajabi, Iran's cyberspace police
            commander. All other applications that are now operating in the
            nation, except from this one, are completely prohibited, according
            to Rajabi.
          </p>
          <p>
            The Tebyan Cultural Institute, a branch of Iran's Islamic Propaganda
            Organization, developed the Hamdam app, which promises to utilise
            "artificial intelligence" to discover suitable matches "only for
            bachelors seeking permanent marriage and a single spouse."
          </p>
          <p>
            Komeil Khojasteh-Tebyan, speaking during the unveiling of the Hamdam
            app, warned that family values were under threat from foreign
            influences. “Family is the devil's goal, and they attempt to force
            their own ideas” on it, he continued, adding that this software will
            aid in the creation of “healthy” families.
          </p>
          <p>
            According to Hamdam's platform, users must pass a "psychological
            exam" and have their identification verified before being allowed to
            use the app and browse. When the right match is identified, the app
            "introduces the families to each other in the presence of service
            advisors," who will "accompanies" the pair for the next four years
            after they marry.
          </p>
          <p>
            According to the app's makers, Hamdam offers a free registration
            since it is "an independent income model. In March, Iran's
            parliament enacted a measure on "population growth and family
            assistance." It directs the government to provide significant
            financial incentives for marriage and to encourage people to have
            more than two children, while restricting access to abortion. Iran's
            political leaders, including Supreme Leader Ayatollah Ali Khamenei,
            have repeatedly cautioned against the country's rising marriage age
            and declining birth rates."
          </p>
        </div>
      </div>
    </div>
  );
};

export default HomeDetail2;
