import React from "react";
import thirtyone from "./31.png";
import {
  Breadcrumb,
  BreadcrumbItem,
  Card,
  CardImg,
  CardBody,
  CardTitle,
} from "reactstrap";
import { Link } from "react-router-dom";

const TechDetail = () => {
  return (
    <div className="container">
      <div className="row p-2">
        <Breadcrumb>
          <BreadcrumbItem>
            <Link to="/home">Home</Link>
          </BreadcrumbItem>
          <BreadcrumbItem>
            <Link to="/tech">Tech</Link>
          </BreadcrumbItem>
          <BreadcrumbItem active>Read</BreadcrumbItem>
        </Breadcrumb>
      </div>
      <div className="row p-3">
        <div className="col-12">
          <Card>
            <CardImg src={thirtyone} />
            <CardBody>
              <CardTitle className="paragraph">
                Netflix is expanding its service to feature video games and has
                on-boarded EA and Facebook executives to head up game
                development.
              </CardTitle>
            </CardBody>
          </Card>
        </div>
      </div>
      <div className="row justify-content-center">
        <div className="col-9">
          <p>
            With an unlimited collection of flicks, cheesy reality programmes,
            satirical cartoons, and high-brow TV dramas to choose from, it's
            reasonable to assume that most Netflix subscribers are getting their
            money's worth. The TV behemoth is head and shoulders above its
            competitors as the most popular streaming service in 2021, with over
            200 million users from 190 countries.
          </p>
          <p>
            Netflix confirmed on Tuesday (July 6) that it is getting into game
            production in a significant manner, after months of rumours dating
            back to May. According to IGN, Netflix has hired Mike Verdu, a
            former executive at EA, Oculus, and Zynga, as Vice President of Game
            Development, and the first batch of games will be released on the
            Netflix app before the end of the year. Nobody knows what this
            expansion will look like yet. Netflix may have grand aspirations to
            develop its own first-party titles, but it's more probable that it's
            planning a licencing blitz for well-known third-party games and
            franchises.
          </p>
        </div>
      </div>
    </div>
  );
};

export default TechDetail;
