import React from "react";
import thirtytwo from "./32.png";
import {
  Breadcrumb,
  BreadcrumbItem,
  Card,
  CardImg,
  CardBody,
  CardTitle,
} from "reactstrap";
import { Link } from "react-router-dom";

const TechDetail2 = () => {
  return (
    <div className="container">
      <div className="row p-2">
        <Breadcrumb>
          <BreadcrumbItem>
            <Link to="/home">Home</Link>
          </BreadcrumbItem>
          <BreadcrumbItem>
            <Link to="/tech">Tech</Link>
          </BreadcrumbItem>
          <BreadcrumbItem active>Read</BreadcrumbItem>
        </Breadcrumb>
      </div>
      <div className="row p-3">
        <div className="col-12">
          <Card>
            <CardImg src={thirtytwo} />
            <CardBody>
              <CardTitle className="paragraph">
                Health AI is now reportedly capable of predicting the year each
                of our immune systems
              </CardTitle>
            </CardBody>
          </Card>
        </div>
      </div>
      <div className="row justify-content-center">
        <div className="col-9">
          <p>
            Health AI is now said to be capable of forecasting when our immune
            systems will eventually fail, as well as whether or not people will
            develop cardiovascular issues later in life. Remember those old
            school apocalyptic websites that used to show live countdowns to our
            random death dates, usually between two grinning skull gifs? What if
            we had something similar in real life? Researchers at Stanford
            University School of Medicine and the Buck Institute for Research in
            Aging have built an AI that can forecast how robust our immune
            systems are and when they will start to fail.
          </p>
          <p>
            This technology, called iAge, determines a person's 'immunological
            age' by measuring the degree of chronic inflammation in their body.
            How many candles should your next birthday cake have? iAge obtains
            these results by searching for indicators of inflammation at a
            molecular level in our blood using a deep neural network. The
            quantity of functioning protein cells termed cytokine – which rally
            immune cells to the location of an infection – can assist the AI's
            algorithm to generally determine when someone's immune system will
            retire, according to research published this week in the Nature
            Aging journal.
          </p>
        </div>
      </div>
    </div>
  );
};

export default TechDetail2;
