import React from "react";
import twentyone from "./21.png";
import twentytwo from "./22.png";
import twentythree from "./23.png";
import twentyfour from "./24.png";
import twentyfive from "./25.png";
import twentysix from "./26.png";
import {
  Breadcrumb,
  BreadcrumbItem,
  CardImg,
  Card,
  CardTitle,
  CardBody,
  CardSubtitle,
} from "reactstrap";
import { Link } from "react-router-dom";

const Culture = () => {
  return (
    <div className="container-fluid">
      <div className="row p-2">
        <Breadcrumb>
          <BreadcrumbItem>
            <Link to="/home">Home</Link>
          </BreadcrumbItem>
          <BreadcrumbItem active>Culture</BreadcrumbItem>
        </Breadcrumb>
      </div>
      <div className="row p-2">
        <div className="col-md-4 col-xs-12 photo">
          <Link to="/culturedetail" className="linking">
            <Card>
              <CardImg src={twentyone} />
              <CardBody>
                <CardSubtitle>By Mehak Fatima - July 31,2021</CardSubtitle>
                <CardTitle className="titling">
                  The United Arab Emirates has donated $23 million to Pakistan’s
                  polio eradication campaign
                </CardTitle>
              </CardBody>
            </Card>
          </Link>
        </div>
        <div className="col-md-4 col-xs-12 photo">
          <Link to="/culturedetail2" className="linking">
            <Card>
              <CardImg src={twentytwo} />
              <CardBody>
                <CardSubtitle>By Farhan Ali - July 31,2021</CardSubtitle>
                <CardTitle className="titling">
                  In Pakistan, a Turkish aid organisation has established a
                  medical facility
                </CardTitle>
              </CardBody>
            </Card>
          </Link>
        </div>
        <div className="col-md-4 col-xs-12 photo">
          <Link to="/culturedetail3" className="linking">
            <Card>
              <CardImg src={twentythree} />
              <CardBody>
                <CardSubtitle>By Ali Hassan - July 31,2021</CardSubtitle>
                <CardTitle className="titling">
                  Lets Talk About Sindh Culture
                </CardTitle>
              </CardBody>
            </Card>
          </Link>
        </div>
      </div>
      <div className="row p-2">
        <div className="col-md-4 col-xs-12 photo">
          <Link to="/culturedetail4" className="linking">
            <Card>
              <CardImg src={twentyfour} />
              <CardBody>
                <CardSubtitle>By Sajid Ali - July 31,2021</CardSubtitle>
                <CardTitle className="titling">
                  It’s no-plastic July! Here, we’ve been making a conscious
                  effort to cut down on our plastic consumption and recycle
                  responsibly when it is unavoidable
                </CardTitle>
              </CardBody>
            </Card>
          </Link>
        </div>
        <div className="photo col-md-4 col-xs-12">
          <Link to="/culturedetail5" className="linking">
            <Card>
              <CardImg src={twentyfive} />
              <CardBody>
                <CardSubtitle>By Raj Kumar - July 31, 2021</CardSubtitle>
                <CardTitle className="titling">
                  California is launching the largest free school lunches
                  program in the US.
                </CardTitle>
              </CardBody>
            </Card>
          </Link>
        </div>
        <div className="photo col-md-4 col-xs-12">
          <Link to="/culturedetail5" className="linking">
            <Card>
              <CardImg src={twentysix} />
              <CardBody>
                <CardSubtitle>By Farzan - July 31, 2021</CardSubtitle>
                <CardTitle className="titling">
                  new species are being discovered by biologists all the time.
                </CardTitle>
              </CardBody>
            </Card>
          </Link>
        </div>
      </div>
    </div>
  );
};

export default Culture;
