import React from "react";
import twentysix from "./26.png";
import {
  Breadcrumb,
  BreadcrumbItem,
  Card,
  CardImg,
  CardBody,
  CardTitle,
} from "reactstrap";
import { Link } from "react-router-dom";

const CultureDetail6 = () => {
  return (
    <div className="container">
      <div className="row p-2">
        <Breadcrumb>
          <BreadcrumbItem>
            <Link to="/home">Home</Link>
          </BreadcrumbItem>
          <BreadcrumbItem>
            <Link to="/culture">Culture</Link>
          </BreadcrumbItem>
          <BreadcrumbItem active>Read</BreadcrumbItem>
        </Breadcrumb>
      </div>
      <div className="row p-3">
        <div className="col-12">
          <Card>
            <CardImg className="detail-img" src={twentysix} />
            <CardBody>
              <CardTitle className="titling">
                new species are being discovered by biologists all the time.
              </CardTitle>
            </CardBody>
          </Card>
        </div>
      </div>
      <div className="row  justify-content-center">
        <div className="col-9 ">
          <p>
            Research into new species by zoologists is essential in helping us
            to protect the animal kingdom and adapt to climate change. This may
            come as a surprise, but there’s a whole lot we don’t know about life
            on Earth. In fact, new species are being discovered by biologists
            all the time. While the last year may have felt uneventful for most,
            scientists were busy naming and describing over 500 previously
            unknown species of monkeys, insects, octopus, and more. Alongside
            this, investigating how wildlife is managing the changes to their
            natural environment – caused by human behaviour – has been critical.
            It’s true the climate is changing faster than most animals can
            adapt. For birds and mammals, biological adaptation such as heat
            resistance and increased water retention are a long process.
          </p>
          <p>
            The good news is many animals put up a decent fight – they’ll adapt
            by changing their behaviours and pass the learned strategies over to
            their young. Scientists have observed songbirds laying their eggs up
            to two weeks earlier, timing the birth of their chicks with the
            survival-window of their food, which is mainly made up of
            caterpillars. This learned behaviour is necessary as insects evolve
            quickly. As the caterpillar senses changes in its environment – such
            as warming temperatures – breeding behaviours will be adjusted to
            optimise its survival. Eagles are migrating earlier, moving to
            cooler areas earlier in the year. Turtles have been seen adjusting
            their routes for nesting and feeding due to altered ocean currents.
            Such changed behaviour isn’t always successful. Continued stress on
            environments has scientists questioning if we’re headed rapidly
            towards the loss of over half of global species.,
          </p>
        </div>
      </div>
    </div>
  );
};

export default CultureDetail6;
