import React from "react";
import twentyone from "./22.png";
import {
  Breadcrumb,
  BreadcrumbItem,
  Card,
  CardImg,
  CardBody,
  CardTitle,
} from "reactstrap";
import { Link } from "react-router-dom";

const CultureDetail2 = () => {
  return (
    <div className="container">
      <div className="row p-2">
        <Breadcrumb>
          <BreadcrumbItem>
            <Link to="/home">Home</Link>
          </BreadcrumbItem>
          <BreadcrumbItem>
            <Link to="/culture">Culture</Link>
          </BreadcrumbItem>
          <BreadcrumbItem active>Read</BreadcrumbItem>
        </Breadcrumb>
      </div>
      <div className="row p-3">
        <div className="col-12">
          <Card>
            <CardImg className="detail-img" src={twentyone} />
            <CardBody>
              <CardTitle className="titling">
                In Pakistan, a Turkish aid organisation has established a
                medical facility
              </CardTitle>
            </CardBody>
          </Card>
        </div>
      </div>
      <div className="row  justify-content-center">
        <div className="col-9 ">
          <p>
            Turkey's government-run assistance agency has opened a medical
            facility in Pakistan for infants born with cleft lip and palate. Al
            Mustafa Medical Center, a charity hospital supported by a local NGO,
            has set up a specialist operating theatre in Karachi's southern port
            city.
          </p>

          <p>
            The facility was inaugurated by Ibrahim Katirci, the coordinator of
            the Turkish Cooperation and Coordination Agency (TIKA), Haji
            Muhammad Hanif Tayyab, the founder of the charity, and provincial
            governor Imran Ismail. The centre would execute up to 1,000
            operations each year, according to a statement from TIKA.
          </p>
        </div>
      </div>
    </div>
  );
};

export default CultureDetail2;
