import React from "react";
import seven from "./7.png";
import {
  Breadcrumb,
  BreadcrumbItem,
  Card,
  CardImg,
  CardBody,
  CardTitle,
} from "reactstrap";
import { Link } from "react-router-dom";

const HomeDetail7 = () => {
  return (
    <div className="container">
      <div className="row p-2">
        <Breadcrumb>
          <BreadcrumbItem>
            <Link to="/home">Home</Link>
          </BreadcrumbItem>
          <BreadcrumbItem active>Read</BreadcrumbItem>
        </Breadcrumb>
      </div>
      <div className="row p-3">
        <div className="col-12">
          <Card>
            <CardImg src={seven} />
            <CardBody>
              <CardTitle className="paragraph">
                Imports of mobile phones increased by more than 50% in FY21
              </CardTitle>
            </CardBody>
          </Card>
        </div>
      </div>
      <div className="row justify-content-center">
        <div className="col-9">
          <p>
            Overall telecom imports into the country increased by 39.33 percent
            from $1.861 billion in 2019-20 to $2.593 billion in 2020-21 during
            the study period (July-June). In June 2021, mobile phone imports
            into Pakistan fell 11.51 percent compared to the same month the
            previous year. Mobile phone imports totalled $204.676 million in
            June 2021, compared to $331.302 million in June 2020, according to
            PBS statistics.
          </p>
          <p>
            Mobile phone imports increased by 16.62 percent month over month in
            June 2021, to $175.501 million, from $175.501 million in May 2021.
            In June 2021, total telecom imports into Pakistan fell 6.30 percent
            compared to the same month the previous year. In June 2021, telecom
            equipment imports totalled $255.943 million, down from $273.166
            million in June 2020.
          </p>
          <p>
            Overall telecom imports increased by 16.02 percent month over month
            in June 2021, reaching $220.599 million, up from $220.599 million in
            May 2021. Other apparatus imports increased by 7.48 percent in
            July-June 2020-21, from $491.163 million in 2019-20 to $527.879
            million in 2020-21.
          </p>
        </div>
      </div>
    </div>
  );
};

export default HomeDetail7;
