import React from "react";
import twentyfive from "./25.png";
import {
  Breadcrumb,
  BreadcrumbItem,
  Card,
  CardImg,
  CardBody,
  CardTitle,
} from "reactstrap";
import { Link } from "react-router-dom";

const CultureDetail5 = () => {
  return (
    <div className="container">
      <div className="row p-2">
        <Breadcrumb>
          <BreadcrumbItem>
            <Link to="/home">Home</Link>
          </BreadcrumbItem>
          <BreadcrumbItem>
            <Link to="/culture">Culture</Link>
          </BreadcrumbItem>
          <BreadcrumbItem active>Read</BreadcrumbItem>
        </Breadcrumb>
      </div>
      <div className="row p-3">
        <div className="col-12">
          <Card>
            <CardImg className="detail-img" src={twentyfive} />
            <CardBody>
              <CardTitle className="titling">
                California is launching the largest free school lunches program
                in the US.
              </CardTitle>
            </CardBody>
          </Card>
        </div>
      </div>
      <div className="row  justify-content-center">
        <div className="col-9 ">
          <p>
            After a huge budget surplus from income tax revenue, California is
            launching the largest free school lunches program in the US. All 6.2
            million public school students in the state are free to join
            regardless of family income. When classrooms reopen for the fall
            term in California, all 6.2 million public school students will have
            the chance to opt into their school’s free meal program for the
            first time. This state-wide pledge has been made possible by an
            unexpected $75 billion budget surplus largely from income tax
            revenue, and $54 million has now been committed to offer free
            lunches for the 2021-2022 fiscal year. Beyond that, a further $650
            million has been set aside to cover the cost of breakfast and lunch
            options throughout the following year. Gone at last are federal laws
            that capped student eligibility at under $34,000 per household.
          </p>

          <p>
            While several US cities including New York, Boston, and Chicago have
            provided free school meals for some years now, California’s proposal
            is the first to go completely state-wide – challenging prior notions
            that the prospect was too costly and unrealistic. California’s
            director of food services, Erin Primer, has lauded the influx of
            funding as ‘historic’ and ‘life changing’ not only in greatly
            increasing the quantity of school meals, but also the quality. ‘The
            extra funding will allow schools to offer tastier, better quality
            food such as fresh bread, produce, and cheese from local producers,’
            she said. At a time where the Golden State found itself flush with
            cash, momentum to upscale food assistance was quickly seized upon
            and driven by a coalition of school officials, lawmakers, charities,
            and parents called School Meals for All.
          </p>
        </div>
      </div>
    </div>
  );
};

export default CultureDetail5;
