import React from "react";
import one from "./1.png";
import {
  Breadcrumb,
  BreadcrumbItem,
  Card,
  CardImg,
  CardBody,
  CardTitle,
} from "reactstrap";
import { Link } from "react-router-dom";

const HomeDetail = () => {
  return (
    <div className="container">
      <div className="row p-2">
        <Breadcrumb>
          <BreadcrumbItem>
            <Link to="/home">Home</Link>
          </BreadcrumbItem>
          <BreadcrumbItem active>Read</BreadcrumbItem>
        </Breadcrumb>
      </div>
      <div className="row p-3">
        <div className="col-12">
          <Card>
            <CardImg className="detail-img" src={one} />
            <CardBody>
              <CardTitle className="titling">
                Mark Bezos, Jeff Bezos, Oliver Daemen, and Mary Wallace Funk
                (from left). — Image courtesy of the Blue Origin Twitter account
              </CardTitle>
            </CardBody>
          </Card>
        </div>
      </div>
      <div className="row  justify-content-center">
        <div className="col-9 ">
          <p className="paragraph">
            On Tuesday, Jeff Bezos flew into space for the first time with
            passengers aboard his rocket business, becoming the second
            billionaire in just over a week to ride his own spaceship.
          </p>
          <p>
            The Amazon CEO was joined by a hand-picked entourage that included
            his brother, an 18-year-old Dutch teen, and an 82-year-old Texas
            aviation pioneer – the youngest and oldest people to ever go in
            space. Blue Origin's New Shepard rocket, named for America's first
            astronaut, launched from rural West Texas on the 52nd anniversary of
            the Apollo 11 moon landing, a date picked by Bezos for its
            historical importance.
          </p>
          <p>
            Even though Richard Branson of Virgin Galactic pushed forward his
            own trip from New Mexico in the battle for space tourist cash, he
            stuck to it and beat him to space by nine days. Unlike Branson's
            piloted rocket jet, Bezos' capsule was fully automated, requiring no
            official personnel on board for the 10-minute up-and-down journey.
          </p>
          <p>
            Blue Origin aimed for an altitude of 66 miles (106 kilometres),
            which would be more than 10 miles (16 kilometres) higher than
            Branson's July 11 voyage. To get the capsule high enough, the
            60-foot (18-meter) booster sped to Mach 3, or three times the speed
            of sound, before separating and aiming for a vertical landing.
          </p>
          <p>
            The passengers were expected to get three to four minutes of
            weightlessness to float around the spacious white capsule. Then the
            window-filled capsule was going to head to a parachute touchdown on
            the desert floor, with Bezos and his guests briefly experiencing
            nearly six times the force of gravity, or 6 G’s, on the way back.
          </p>
          <p>
            Sharing Bezos’ dream-come-true adventure was Wally Funk, from the
            Dallas area, one of 13 female pilots who went through the same tests
            as NASA’s all-male astronaut corps in the early 1960s but never made
            it into space. Joining them on the ultimate joyride was the
            company’s first paying customer, Oliver Daemen, a last-minute
            fill-in for the mystery winner of a $28 million auction who opted
            for a later flight. The Dutch teen’s father took part in the
            auction, and agreed on a lower undisclosed price last week when Blue
            Origin offered his son the vacated seat.
          </p>
        </div>
      </div>
    </div>
  );
};

export default HomeDetail;
