import React from "react";
import fifteen from "./15.png";
import {
  Breadcrumb,
  BreadcrumbItem,
  Card,
  CardImg,
  CardBody,
  CardTitle,
} from "reactstrap";
import { Link } from "react-router-dom";

const StyleDetail5 = () => {
  return (
    <div className="container">
      <div className="row p-2">
        <Breadcrumb>
          <BreadcrumbItem>
            <Link to="/home">Home</Link>
          </BreadcrumbItem>
          <BreadcrumbItem>
            <Link to="/style">Style</Link>
          </BreadcrumbItem>
          <BreadcrumbItem active>Read</BreadcrumbItem>
        </Breadcrumb>
      </div>
      <div className="row p-3">
        <div className="col-12">
          <Card>
            <CardImg className="detail-img" src={fifteen} />
            <CardBody>
              <CardTitle>
                Pride is different this year, with celebrations cancelled or
                postponed.
              </CardTitle>
            </CardBody>
          </Card>
        </div>
      </div>
      <div className="row  justify-content-center">
        <div className="col-9 ">
          <p>
            Pride is different this year, with celebrations cancelled or
            postponed. Even so, there are plenty of ways to show support by
            donating to charities or shopping with brands who give back. Time is
            up for performance activism. As young consumers, we are far more
            aware of the philosophical values of the companies we choose to
            purchase from than previous generations. Brands must now align their
            values with ours, be vocal about their cause, and turn those values
            into hard action – or face the music. Tokenistic marketing such as
            ‘greenwashing’ and ‘pink-washing’ has been a common practice for
            quite some time, particularly as social change becomes increasingly
            favourable with the public.
          </p>
          <p>
            Gen Z are acutely aware of which brands are genuine, however, and
            can identify whose ethics have been shaped by a desire for truth,
            transparency, and maximising opportunities for individual
            self-expression. In 2021, rainbow branded products and social media
            posts won’t fill the bill if brands aren’t doing anything to give
            back to LGBTQ+ communities they claim to support. Luckily, there are
            plenty of beauty companies who are putting their money where their
            mouth is this Pride Month. We’re breaking down the list of brands
            who have pledged to donate product sales to the community.
          </p>
          <p>
            Well known LA-based make up brand Morphe has developed a limited
            edition ‘Live With Love’ eyeshadow palette which includes a range of
            25 vibrant hues. All the proceeds from the palette will go to The
            Trevor Project, which is the world’s largest 24/7 suicide and crisis
            prevention organisation for LGBTQ+ youth. The Trevor Project
            currently runs several operations, such as an education program for
            youth-serving adults and organisations, advocacy departments for
            pro-LGBTQ+ legislation and against anti-LGBTQ+ policy, plus a
            dedicated research team which seeks for the best ways to help the
            community in crisis.
          </p>
        </div>
      </div>
    </div>
  );
};

export default StyleDetail5;
