import React from "react";
import three from "./3.png";
import {
  Breadcrumb,
  BreadcrumbItem,
  Card,
  CardImg,
  CardBody,
  CardTitle,
} from "reactstrap";
import { Link } from "react-router-dom";

const HomeDetail3 = () => {
  return (
    <div className="container">
      <div className="row p-2">
        <Breadcrumb>
          <BreadcrumbItem>
            <Link to="/home">Home</Link>
          </BreadcrumbItem>
          <BreadcrumbItem active>Read</BreadcrumbItem>
        </Breadcrumb>
      </div>
      <div className="row p-3">
        <div className="col-12">
          <Card>
            <CardImg src={three} />
            <CardBody>
              <CardTitle className="paragraph">
                Apple accused of slowing down iPhone 12, iPhone 11, and other
                models with iOS updates
              </CardTitle>
            </CardBody>
          </Card>
        </div>
      </div>
      <div className="row justify-content-center">
        <div className="col-9">
          <p>
            Apple, according to insiders, has once again fallen behind another
            administrative agency's schedule. Apple has been criticised by
            Spain's Consumer Protection Organization OCU (via 9to5Mac) for
            strangling the performance of the iPhone 12, iPhone 11, iPhone XS,
            and iPhone 8 with iOS 14.5, iOS 14.5.1, and iOS 14.6 upgrades.
          </p>
          <p>
            They said that the gadgets experienced corrupted battery life and
            performance in the wake of introducing the updates. The OCU needs
            Apple to resolve the issue and remunerate affected clients likewise.
            It is hazy what is the issue, and it influenced the number of
            clients.
          </p>
          <p>
            Previously, it was claimed that the iPhone XR was receiving a lot of
            attention after receiving the iOS 14.5.1 upgrade. In certain created
            benchmarks, it performed similarly to the iPhone 12 and even
            outperformed the iPhone 11. Given that both the iPhone 11 and iPhone
            12 functioned well within spec, there was no reason to make any
            assumptions. All things considered, the news was good for the iPhone
            XR clients as their gadgets got a fresh chance to take life by the
            horns.
          </p>
          <p>
            Surprisingly, the OCU cites "media reports" as a basis for the
            alleged delay. There is hardly no information about it in the
            international press. Perhaps the situation differs from what is
            reported in Spanish media. The upgrades are likely to have affected
            just a small number of iPhones in Spain. Given that the lead iPhone
            12 series is rumoured to have had a presentation halt, it's a topic
            worth investigating. More insights regarding the case ought to arise
            once the suit goes to court, which appears to be unavoidable now.
          </p>
        </div>
      </div>
    </div>
  );
};

export default HomeDetail3;
