import React from "react";
import eleven from "./11.png";
import twelve from "./12.png";
import thirteen from "./13.png";
import fourteen from "./14.png";
import fifteen from "./15.png";
import sixteen from "./16.png";
import {
  Breadcrumb,
  BreadcrumbItem,
  CardImg,
  Card,
  CardTitle,
  CardBody,
  CardSubtitle,
} from "reactstrap";
import { Link } from "react-router-dom";

const Style = () => {
  return (
    <div className="container-fluid">
      <div className="row p-2">
        <Breadcrumb>
          <BreadcrumbItem>
            <Link to="/home">Home</Link>
          </BreadcrumbItem>
          <BreadcrumbItem active>Style</BreadcrumbItem>
        </Breadcrumb>
      </div>
      <div className="row p-2">
        <div className="col-md-4 col-xs-12 photo">
          <Link to="/styledetail" className="linking">
            <Card>
              <CardImg src={eleven} />
              <CardBody>
                <CardSubtitle>By Farhan Ali - July 31,2021</CardSubtitle>
                <CardTitle className="titling">
                  The desire for authentic human hair wigs is growing
                </CardTitle>
              </CardBody>
            </Card>
          </Link>
        </div>
        <div className="col-md-4 col-xs-12 photo">
          <Link to="/styledetail2" className="linking">
            <Card>
              <CardImg src={twelve} />
              <CardBody>
                <CardSubtitle>By Farman - July 31,2021</CardSubtitle>
                <CardTitle className="titling">
                  it’s pretty standard to see a lengthy list of ingredients on
                  almost every item
                </CardTitle>
              </CardBody>
            </Card>
          </Link>
        </div>
        <div className="col-md-4 col-xs-12 photo">
          <Link to="/styledetail3" className="linking">
            <Card>
              <CardImg src={thirteen} />
              <CardBody>
                <CardSubtitle>By Mehak Fatima - July 31,2021</CardSubtitle>
                <CardTitle className="titling">
                  An outlandish concept that successfully tackles growing
                  concerns of sustainability
                </CardTitle>
              </CardBody>
            </Card>
          </Link>
        </div>
      </div>
      <div className="row p-2">
        <div className="col-md-4 col-xs-12 photo">
          <Link to="/styledetail4" className="linking">
            <Card>
              <CardImg src={fourteen} />
              <CardBody>
                <CardSubtitle>By Mehak Fatima - July 31,2021</CardSubtitle>
                <CardTitle className="titling">
                  The future looks fluid: genderless fashion explained
                </CardTitle>
              </CardBody>
            </Card>
          </Link>
        </div>
        <div className="photo col-md-4 col-xs-12">
          <Link to="/styledetail5" className="linking">
            <Card>
              <CardImg src={fifteen} />
              <CardBody>
                <CardSubtitle>By Raj Kumar - July 31, 2021</CardSubtitle>
                <CardTitle className="titling">
                  Pride is different this year, with celebrations cancelled or
                  postponed.
                </CardTitle>
              </CardBody>
            </Card>
          </Link>
        </div>
        <div className="photo col-md-4 col-xs-12">
          <Link to="/styledetail6" className="linking">
            <Card>
              <CardImg src={sixteen} />
              <CardBody>
                <CardSubtitle>By Farzan - July 31, 2021</CardSubtitle>
                <CardTitle className="titling">
                  Though the number of people shopping consciously has soared in
                  recent years
                </CardTitle>
              </CardBody>
            </Card>
          </Link>
        </div>
      </div>
    </div>
  );
};

export default Style;
