import React from "react";
import six from "./6.png";
import {
  Breadcrumb,
  BreadcrumbItem,
  Card,
  CardImg,
  CardBody,
  CardTitle,
} from "reactstrap";
import { Link } from "react-router-dom";

const HomeDetail6 = () => {
  return (
    <div className="container">
      <div className="row p-2">
        <Breadcrumb>
          <BreadcrumbItem>
            <Link to="/home">Home</Link>
          </BreadcrumbItem>
          <BreadcrumbItem active>Read</BreadcrumbItem>
        </Breadcrumb>
      </div>
      <div className="row p-3">
        <div className="col-12">
          <Card>
            <CardImg src={six} />
            <CardBody>
              <CardTitle className="paragraph">
                HOW TO MAKE WINDOWS 10 LOOK LIKE WINDOWS 11
              </CardTitle>
            </CardBody>
          </Card>
        </div>
      </div>
      <div className="row justify-content-center">
        <div className="col-9">
          <p>
            On the 24th of June, Microsoft released Windows 11. They've even
            started rolling out the Windows 11 Insider Preview on the developer
            channel after a week. Many users who followed our prior instructions
            on Windows 11 Insider Preview have already begun using the new
            release. However, Microsoft has imposed several perplexing
            restrictions, preventing many PCs from running Windows 11. So if you
            guys want to experience Windows 11’s look and feel, you can do it on
            your Windows 10 system. In this article, we will discuss how to make
            Windows 10 look like Windows 11. Also, if you are bored with the
            Windows 10 visuals, then also you can try this. This article will
            give your Windows 10 a full makeover.
          </p>
          <p>
            Currently, the operating system is only accessible as part of a
            developer-focused Preview. Sign up for Microsoft's Windows Insider
            programme to get the Windows 11 Preview, but most people should just
            wait for the consumer release. Of course, you might be curious as to
            when you can get your hands on Windows 11 for yourself. Or, exactly
            what’s new in the operating system. Here’s everything you need to
            know about Microsoft’s next operating system.
          </p>
          <p>
            Windows 11 introduces a slew of exciting new features that have the
            potential to dramatically transform your Windows experience. Here
            are a few of our favourite features that are available. Changes in
            appearance You'll probably notice aesthetic changes to the operating
            system before diving into any technological features. The fact that
            the Start Menu and Task Bar are now in the centre of the screen is
            perhaps the most noticeable change.
          </p>
          <p>
            Apple allowed iOS apps to work on macOS, and now Microsoft is making
            a similar change — by bringing Android apps to Windows 11. But the
            way Microsoft is doing this is a little strange — it has partnered
            with Amazon to bring the Amazon Appstore inside the newly revamped
            Microsoft Store. In other words, you’ll be limited in the Android
            apps that you can actually install on your computer. Thankfully,
            Windows 11 does allow sideloading Android apps.
          </p>
        </div>
      </div>
    </div>
  );
};

export default HomeDetail6;
