import React from "react";
import thirtyfive from "./35.png";
import {
  Breadcrumb,
  BreadcrumbItem,
  Card,
  CardImg,
  CardBody,
  CardTitle,
} from "reactstrap";
import { Link } from "react-router-dom";

const TechDetail5 = () => {
  return (
    <div className="container">
      <div className="row p-2">
        <Breadcrumb>
          <BreadcrumbItem>
            <Link to="/home">Home</Link>
          </BreadcrumbItem>
          <BreadcrumbItem>
            <Link to="/tech">Tech</Link>
          </BreadcrumbItem>
          <BreadcrumbItem active>Read</BreadcrumbItem>
        </Breadcrumb>
      </div>
      <div className="row p-3">
        <div className="col-12">
          <Card>
            <CardImg src={thirtyfive} />
            <CardBody>
              <CardTitle className="paragraph">
                Jeff Bezos was on board the first human flight for his space
                company Blue Origin today
              </CardTitle>
            </CardBody>
          </Card>
        </div>
      </div>
      <div className="row justify-content-center">
        <div className="col-9">
          <p>
            I wonder if CEO entrepreneur Jeff Bezos ever envisioned himself
            being launched into the heavens via a rocket built by his own
            private company when he founded Amazon all those years ago. That’s
            exactly what happened today, though, as Blue Origin launched its
            first ever human flight to space successfully – Jeff himself
            strapped inside a re-usable rocket called New Shepard. It is all
            part of his efforts to fund space travel and make commercial rocket
            ships a reality. Once you run out of things to sell on the ground
            you might as well try your luck at the universe itself, eh? Jeff was
            joined by his brother Mark Bezos, veteran space racer Wally Funk,
            and an 18-year-old student. All four returned to the ground safely.
            He joins a growing list of billionaire tech entrepreneurs eager to
            get ahead in the competition for space tourism. Virgin CEO Richard
            Branson took to the skies himself just a few days ago, beating Bezos
            in speed but not altitude.
          </p>
          <p>
            There are a variety of reasons for the growing interest in space
            travel. For one, it would give tech and travel companies such as
            Virgin and SpaceX a significant advantage in a new market for
            commercial space exploration. Being the first to provide public
            seats inside rockets would be a big deal, and no self-respecting
            billionaire wants to be seen missing out. Elon Musk has also said he
            wants to eventually colonise nearby planets such as Mars and
            believes humans shouldn’t be a ‘single planet species’. Whether you
            view that as an abandonment of Earth – given the looming threat of
            climate change – or a valiant attempt to broaden our galactic
            ambitions is up to you. Billionaires also claim that their work in
            this industry will help solve current problems down on the surface.
          </p>
        </div>
      </div>
    </div>
  );
};

export default TechDetail5;
