import React, { Component } from "react";
import { Redirect, Route, Switch } from "react-router-dom";
import Header from "./Header";
import Footer from "./Footer";
import Home from "./Home";
import HomeDetail from "./HomeDetail";
import HomeDetail2 from "./HomeDetail2";
import HomeDetail3 from "./HomeDetail3";
import HomeDetail4 from "./HomeDetail4";
import HomeDetail5 from "./HomeDetail5";
import HomeDetail6 from "./HomeDetail6";
import HomeDetail7 from "./HomeDetail7";
import Style from "./Style";
import StyleDetail from "./StyleDetail";
import StyleDetail2 from "./StyleDetail2";
import StyleDetail3 from "./StyleDetail3";
import StyleDetail4 from "./StyleDetail4";
import StyleDetail5 from "./StyleDetail5";
import StyleDetail6 from "./StyleDetail6";
import Culture from "./Culture";
import CultureDetail from "./CultureDetail";
import CultureDetail2 from "./CultureDetail2";
import CultureDetail3 from "./CultureDetail3";
import CultureDetail4 from "./CultureDetail4";
import CultureDetail5 from "./CultureDetail5";
import CultureDetail6 from "./CultureDetail6";
import Tech from "./Tech";
import TechDetail from "./TechDetail";
import TechDetail2 from "./TechDetail2";
import TechDetail3 from "./TechDetail3";
import TechDetail4 from "./TechDetail4";
import TechDetail5 from "./TechDetail5";
import TechDetail6 from "./TechDetail6";
import Error from "./Error";

class Main extends Component {
  render() {
    return (
      <div>
        <Header />
        <Switch>
          <Route exact path="/home" component={Home} />
          <Route path="/homedetail" component={HomeDetail} />
          <Route path="/homedetail2" component={HomeDetail2} />
          <Route path="/homedetail3" component={HomeDetail3} />
          <Route path="/homedetail4" component={HomeDetail4} />
          <Route path="/homedetail5" component={HomeDetail5} />
          <Route path="/homedetail6" component={HomeDetail6} />
          <Route path="/homedetail7" component={HomeDetail7} />

          <Route exact path="/style" component={Style} />
          <Route path="/styledetail" component={StyleDetail} />
          <Route path="/styledetail2" component={StyleDetail2} />
          <Route path="/styledetail3" component={StyleDetail3} />
          <Route path="/styledetail4" component={StyleDetail4} />
          <Route path="/styledetail5" component={StyleDetail5} />
          <Route path="/styledetail6" component={StyleDetail6} />

          <Route exact path="/culture" component={Culture} />
          <Route path="/culturedetail" component={CultureDetail} />
          <Route path="/culturedetail2" component={CultureDetail2} />
          <Route path="/culturedetail3" component={CultureDetail3} />
          <Route path="/culturedetail4" component={CultureDetail4} />
          <Route path="/culturedetail5" component={CultureDetail5} />
          <Route path="/culturedetail6" component={CultureDetail6} />

          <Route exact path="/tech" component={Tech} />
          <Route path="/techdetail" component={TechDetail} />
          <Route path="/techdetail2" component={TechDetail2} />
          <Route path="/techdetail3" component={TechDetail3} />
          <Route path="/techdetail4" component={TechDetail4} />
          <Route path="/techdetail5" component={TechDetail5} />
          <Route path="/techdetail6" component={TechDetail6} />

          <Route path="/home" component={Error} />

          <Redirect to="/home" />
        </Switch>
        <Footer />
      </div>
    );
  }
}
export default Main;
